<template>
  <div>
    <NavTemplent title="部门负责人指标库"></NavTemplent>
    <div style="padding: 15px 10% 15px 15px;">
      <div>
        <h5 style="font-weight: bold">数据筛选</h5>
        <el-form style="margin-top: 15px" label-width="80px" inline @submit.native.prevent size="mini">
          <el-date-picker
            @change="getStatisticalList"
            size="mini"
            v-model="form.date"
            type="daterange"
            format="yyyy/MM/dd"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期">
          </el-date-picker>
          <el-form-item label="部门">
            <el-select v-model="form.department_id" placeholder="请选择部门" @change="getStatisticalList">
              <el-option v-for="item in departmentList" :key="item.department_id" :value="item.department_id" :label="item.department_name"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="周期">
            <el-select v-model="form.cycle" placeholder="请选择站点" @change="getStatisticalList">
              <el-option :value="0" label="日"></el-option>
              <el-option :value="1" label="月"></el-option>
              <el-option :value="2" label="年"></el-option>
            </el-select>
          </el-form-item>
<!--          <el-form-item>-->
<!--            <el-button type="primary" @click="getStatisticalList">-->
<!--              <i class="el-icon-search"></i>-->
<!--              筛选</el-button>-->
<!--          </el-form-item>-->
        </el-form>
      </div>
      <el-row style="border: 1px solid #e6e3e3" v-for="item in statisticalList" :key="item.department_id">
        <el-col :span="24" style="padding: 0 15px">
          <p style="font-size: 14px;font-weight: bolder;padding: 0 15px">
            <span style="text-align: left">{{item.department_name}}</span>
            <span style="text-align: right;cursor: pointer" @click="openDetails(item)">查看详情</span>
          </p>
          <el-row>
            <el-col :span="6" style="padding: 5px;text-align: center" v-for="items in item.quota_list" :key="items.quota_id">
              <p style="font-weight: bolder">{{items.quota_name}}</p>
              <el-progress type="circle" v-if="items.total_complete_value !== '0' || items.total_target_value !== '0'" :stroke-width="10" percentage="0"></el-progress>
              <el-progress type="circle" v-else :stroke-width="10" :percentage="items.total_complete_value / items.total_target_value"></el-progress>
            </el-col>
          </el-row>
        </el-col>
      </el-row>
    </div>
    <el-dialog
      :title="title"
      :visible.sync="dialogVisible"
      width="750px">
      <div id="myChart" style="width: 100%;height: 400px"></div>
      <el-table
        :data="getDepartmentHead"
        border
        style="width: 100%">
        <el-table-column
          type="index">
        </el-table-column>
        <el-table-column
          label="姓名">
          <template slot-scope="scope">
            <span>{{scope.row.user_name || '--'}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="指标名称">
          <template slot-scope="scope">
            <span>{{scope.row.quota_name || '--'}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="时间">
          <template slot-scope="scope">
            <span>{{scope.row.complete_date * 1000 | dateFormat('YYYY/MM/DD')}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="录入量">
          <template slot-scope="scope">
            <span>{{scope.row.target_value}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="完成度">
          <template slot-scope="scope">
            <span>{{ scope.row.complete_value / scope.row.target_value}}</span>
          </template>
        </el-table-column>
      </el-table>
      <div slot="footer" class="dialog-footer">
        <el-button size="mini" type="danger" @click="cancle">取 消</el-button>
        <el-button size="mini" type="primary" @click="save">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
    import moment from 'moment';
    import NavTemplent from "../../../components/NavTemplent";
    export default {
        components: {
            NavTemplent
        },
        data() {
            return {
                listData: [],
                statisticalList: [],
                getDepartmentHead: [],
                departmentList: [],
                dateList: [],
                valueList: [],
                page: {
                    pageSize: 1,
                    pageNum: 50
                },
                dialogVisible: false,
                total: 0,
                title: '查看部门负责人详情',
                form: {
                    cycle: 0,
                    date: [moment(moment().valueOf() - 7 * 24 * 3600 * 1000).format('YYYY/MM/DD'), moment(moment() + 24 * 3600 * 1000).format('YYYY/MM/DD')],
                    department_id: ''
                }
            }
        },
        methods: {
            drawLine() {
                // 基于准备好的dom，初始化echarts实例
                let myChart = this.$echarts.init(document.getElementById('myChart'));
                // 绘制图表
                myChart.setOption({
                    title: {text: '各部门指标数据'},
                    xAxis: {
                        type: 'category',
                        boundaryGap: false,
                        data: this.dateList
                    },
                    yAxis: {
                        type: 'value'
                    },
                    series: [{
                        data: this.valueList,
                        type: 'line',
                        areaStyle: {}
                    }]
                });
            },
            getStatisticalList() {
                this.startTime = this.$root.$options.filters.dateFormat(this.form.date[0], 'YYYY/MM/DD');
                this.lastTime = this.$root.$options.filters.dateFormat(this.form.date[1], 'YYYY/MM/DD');
                this.$post('getDepartmentQuotaList', {
                    start_time: this.startTime,
                    end_time: this.lastTime,
                    department_id: this.form.department_id
                }).then(res => {
                    this.statisticalList = res;
                })
            },
            openDetails(data) {
                this.dialogVisible = true;
                this.startTime = this.$root.$options.filters.dateFormat(this.form.date[0], 'YYYY/MM/DD');
                this.lastTime = this.$root.$options.filters.dateFormat(this.form.date[1], 'YYYY/MM/DD');
                this.$post('getDepartmentQuotaDetail', {
                    start_time: this.startTime,
                    end_time: this.lastTime,
                    department_id: data.department_id
                }).then(res => {
                    this.getDepartmentHead = res.list;
                    this.dateList = res.quota_chart.map(item => {
                        return item.date
                    });
                    this.valueList = res.quota_chart.map(item => {
                        return item.value
                    });
                    if (res.quota_chart) {
                        this.$nextTick(() => {
                            this.drawLine()
                        })
                    }
                });
            },
            cancle() {
                this.dialogVisible = false;
                this.$emit('close')
            },
            // 部门列表
            getDepartmentList() {
                this.$post('/getDepartmentList').then(res => {
                    this.departmentList = res.list
                }).catch(err => {
                    this.$message.error(err)
                });
            }
        },
        mounted() {
            this.getStatisticalList();
            this.getDepartmentList();
        }
    }
</script>

<style lang="scss" scoped>
  .tableTitle{
    padding: 15px;
    h5{
      font-weight: bolder;
    }
  }
  .el-select{
    width: 100%;
  }
  .tab {
    margin: 10px;
    .active{
      background: #409EFF;
      color: #fff;
    }
    span {
      font-size: 14px;
      border-radius: 5px;
      border: 1px solid #e6e3e3;
      padding: 5px 10px;
    }
  }
</style>



